export function convertToDateString(string: Date) {
    return new Date(string).toDateString()
}

export function getEplipsisText(value: string, limit?: number) {
    if (!value) {
        return ""
    } else {
        return value.substr(0, (limit || 20)) + (value.length > (limit || 20) ? "..." : "")
    }
}

export function convertToCamelCase(string: string) {
    return (string.substring(0, 1).toUpperCase() + string.substr(1, string.length))
}